.subtotal{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    height:100px;
    padding:20px;
    background-color: #f3f3f3;
    background-color: #dddddd;
    border-radius: 3px;
}

.subtotal_gift{
    display: flex;
    align-items: center;
}

.subtotal_gift > input{
    margin-right: 10px;
}

.subtotal > button{
    background-color: #f0c14b;
    border-radius: 2px;
    
}